.banner {
    width: 100%;
}

.products {
    background: #F8F8F8;
}

.main {
    width: 1000px;
    margin: 0 auto;
}

/*产品中心*/

.lcy-index-title {
    text-align: center;
}

.title {
    font-size: 30px;
    color: #333;
    margin: 40px 0 0;
}

.lcy-index-title .xian {
    width: 25px;
    height: 5px;
    background: #C2955D;
    margin: 8px auto 40px;
}

.product-list li {
    position: relative;
    float: left;
    width: 240px;
    height: 320px;
    overflow: hidden;
    margin: 0 13px 40px 0;
    background: #fff;
}
.product-list li a{
    display: block;
    height: 100%;
}
.product-list li:nth-child(4n) {
    margin: 0 0 40px 0;
}

.lcy-slide-cont .type {
    font-size: 25px;
    color: #333;
    padding: 10px 0px 10px 0px;
    width: 200px;
    margin: 0 auto;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.lcy-slide-cont .explain {
    color: #C2955D;
    font-size: 16px;
    width: 200px;
    margin: 0 auto;
    position: relative;
    z-index: 9;
    display: none;
}

.product-list .product {
    width: 230px;
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
    transition: all .3s;
}

.product-list .logo-bai {
    width: 30px;
    position: absolute;
    bottom: 20px;
    right: 20px;
}

.product-list li:hover .explain {
    display: block;
}

.product-list li:hover .product {
    opacity: .4;
    transform: scale(.9) translate(12px, 12px);
}

/*申请流程*/

.lcy-flow-diagram {
    width: 100%;
    background: #fff;
    padding-bottom: 70px;
}

.lcy-little-title {
    text-align: center;
    padding: 40px 0;
}

.lcy-little-title i {
    width: 6px;
    height: 16px;
    margin-right: 9px;
    background: #C2955D;
    display: inline-block;
}

.lcy-little-title span {
    font-size: 18px;
    color: #333;
}

.lcy-flow-diagram img {
    width: 682px;
    display: block;
    margin: 0px auto;
}
/* 常见问题 */
.lcy-faq {
    width: 100%;
    padding-bottom: 40px;
}

.lcy-faq .lcy-example {
    width: 1068px;
    margin: 0 auto;
    padding: 0 0 20px;
    border-bottom: 1px solid #DEDEDE;
}

.lcy-faq .lcy-example li {
    width: 534px;
    float: left;
    font-size: 14px;
    color: #333;
    /* height:130px; */
    /* border-bottom: 1px dashed #ECECEC; */
    padding: 20px 17px 20px 0;
    line-height: 22px;
}
.lcy-faq li p {
    margin:0;
}
.lcy-faq .lcy-question {
    width: 100%;
}

.lcy-faq .icon {
    width: 16px;
    margin: 3px 7px 0 3px;
}

.lcy-faq .lcy-question .lcy-question-con{
    font-weight: bold;
    /* width: 456px; */
    float: left;
    margin-bottom:3px;
}

.lcy-faq .lcy-answer {
    width: 100%;
}

.lcy-faq .lcy-answer .lcy-answer-cont {
    width: 456px;
    /* display: -webkit-box; */
    /* -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
    height: 86px; */
    line-height: 25px;
    padding-bottom: 20px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
    max-height: 80px;
}
.lcy-example li:nth-child(1) .lcy-answer-cont,
.lcy-example li:nth-child(2) .lcy-answer-cont
{
    border-bottom: 1px dashed #ECECEC;
}

.lcy-faq li .lcy-particulars {
    font-size: 14px;
    color: #C2955D;
    margin-left: 24px;
}

.lcy-faq .lcy-see-more {
    font-size: 16px;
    color: #C2955D;
    width: 150px;
    display: block;
    margin: 0 auto;
    text-align: center;
}
