.ldDownloadApp {
    height: 540px;
    background: url(../../assets/images/bg_banner2@3x.png) no-repeat center center;
    width: 100%;
    background-size: cover;
}

.ldDownloadApp-main {
    width: 920px;
    min-width: 920px;
    margin: 0 auto;
    position: relative;
}

.ldDownloadApp-font {
    padding-top: 120px;
}

.ldDownloadApp-font h3 {
    color: #fff;
    font-size: 40px;
    font-weight: 600;
    margin:0;
}

.ldDownloadApp-font p.ldDownloadApp-font-P {
    color: #fff;
    font-size: 18px;
    margin:9px 0 28px;
}

.ldDownloadApp-font .down-icon-img img {
    display: block;
    width: 440px;
}

.ldDownloadApp-font .down-icon {
    display: block;
    width: 170px;
    padding: 36px 0 0;
}

.ldDownloadApp-font .down-icon img {
    display: block;
    width: 170px;
}

.ld-app-down-entrance {
    position: relative;
}

.ld-app-down-entrance .down-icon-erwm {
    width: 440px;
    background: #fff;
    position: absolute;
    top: 100%;
    z-index: 999;
}

.ld-app-down-entrance .down-icon-erwm p {
    color: #666;
    font-size: 14px;
    text-align: center;
    padding: 15px 0 9px;
    margin:0;
}

.ld-app-down-entrance .down-icon-erwm .erw-img {
    float: left;
    width: 50%;
    margin: 0 auto;
    text-align: center;
    position: relative;
}

.ld-app-down-entrance .down-icon-erwm .erw-img i {
    position: absolute;
    border-right: 1px dashed #ECECEC;
    height: 160px;
    display: block;
    top: 10px;
    right: 0;
}

.ld-app-down-entrance .down-icon-erwm .erw-img img {
    display: block;
    margin: 0 auto 0;
    width: 140px;
    height: 140px;
}

.ld-app-down-entrance .down-icon-erwm .erw-img p {
    color: #C2955D;
    font-size: 14px;
    padding: 7px 0 15px;
}

.phone-group-img {
    position: absolute;
    width: 430px;
    right: -20px;
    bottom: -123px;
}