*{
    margin:0;
    padding:0;
}
body{
    background:rgba(248,248,248,1);
}
.newsclick{
    width:800px;
    margin:0 auto;
}
.newsclick ul{
    background:rgba(255,255,255,1);
    height:60px;
    width:100%;
    box-shadow:0px 1px 1px 0px rgba(0,0,0,0.1);
    margin:20px 0 30px 0;
    display:flex;
    align-items:center;
}
.About_Us{
    height:20px;
    width:25%;
    font-size:16px;
    text-align:center;
    line-height:20px;
    float:left;
    color:rgba(153,153,153,1);
    border-right:1px solid rgba(216,216,216,1);
}
.About_Usss{
    color:rgba(153,153,153,1);
}
.About_Usss:hover{
    /* color:rgba(153,153,153,1); */
    color:rgba(194,149,93,1);
    font-weight:bold;
    cursor: pointer;
}
.us_color{
    width:24%;
    height:20px;
    line-height:20px;
    font-size:16px;
    text-align:center;
    float:left;
    font-weight:bold;
    color:rgba(194,149,93,1);
    border-right:1px solid rgba(216,216,216,1);
}
.uscolorsf{
    color:rgba(194,149,93,1);
    font-weight:bold;
}
.newsclick ul li:last-child{
    border-right:0;
}
/* 内容 */
.intobei{
    width:100%;
    height:180px;
    border-bottom:1px solid rgba(227,233,238,1);
    padding:30px 20px 30px 20px;
    background:rgba(255,255,255,1);
    display:flex;
    flex-direction:row;
    justify-content:space-between;
}
.intobei:hover{
    box-shadow:1px 1px 1px solid rgba(236,236,236,0.6);
}
.intobei img{
    width:160px;
    height:120px;
}
.Contenttext{
    width:580px;
}
.Contenttext p:last-child{
    font-size:12px;
    color:rgba(153,153,153,1);
    line-height:12px;
}
.TItletext{
    font-size:18px;
    font-weight:bold;
    color:rgba(51,51,51,1);
    line-height:18px;
}
.TItletext:hover{
    color:rgba(0,161,234,0.8);
}
.ENtertime{
    width:580px;
    height:43px;
    font-size:14px;
    color:rgba(102,102,102,1);
    line-height:22px;
    margin:20px 0 26px 0;
}
/* 分页 */
.Paing{
    width:597px;
    height:73px;
    background:rgba(248,248,248,1);
    margin:20px auto 27px auto;
}
.Paing ul{
    margin:0 auto;
    box-shadow:0px 0px 0px 0px;
    background:rgba(248,248,248,1);
}

/* 详情页 */
.introList{
    padding:0px 20px 0px 20px;
    background:rgba(255,255,255,1);
    text-align:center;
    margin-bottom:65px;
}
.a_cloud{
    width:100%;
    font-size:18px;
    font-weight:bold;
    color:rgba(51,51,51,1);
    line-height:18px;
    padding:30px 0 10px 0;
}
.Times_2018{
    font-size:12px;
    color:rgba(153,153,153,1);
    line-height:12px;
}
.EnterMonth{
    font-size:14px;
    color:rgba(102,102,102,0.7);
    line-height:22px;
    text-align:left;
    padding-bottom:20px;
}
.EnterMonth img{
    width:100%;
}
.the_ray{
    color:#666666;
    margin-top:15px;
}