* {
    margin: 0;
    padding: 0;
}

.home {
    background: #fff;
    border-bottom:1px solid transparent;
}

.home p {
    margin-bottom: 0;
}

.arrows {
    position: absolute;
    top: 50%;
    width: 100%;
    height: 0;
    padding: 0 41px;
}

.arrows img {
    height: 30px;
    cursor: pointer;
}

.advantage {
    background: #fff;
}

.advantageContent {
    width: 699px;
    margin: 0 auto;
    text-align: center;
    padding: 30px 0 68px 0;
}

.title:after {
    width: 25px;
    height: 5px;
    background: #C2955D;
    position: absolute;
    content: '';
    top: 57px;
    left: 50%;
    transform: translateX(-50%);
}

.advantageImg {
    display: flex;
    justify-content: space-between;
}

.advantageImg img {
    width: 80px;
    height: 80px;
    display: block
}

.advantageImg text {
    font-size: 18px;
    color: #333;
    display: block;
    margin-top: 16px;
}

/* 我们的优势下面的深蓝色图片 */

.measure {
    width: 100%;
    height: 500px;
    background: url('../../assets/images/bg_educesuan.png') no-repeat;
    background-size: 100% 100%;
    padding: 100px 0;
}

.measureContent {
    width: 1000px;
    margin: auto auto;
    display: flex;
}

.measureLeft {
    width: 680px;
    height: 300px;
    position: relative;
    /* background: rgba(0, 29, 45, 0.8) url('../../assets/images/pic_limit.png') no-repeat 147px 82px; */
    background-size: 372px 71px;
    border-radius: 3px;
    border: 1px solid #C2955D;
    text-align: center;
    overflow: hidden;
    background:rgba(0,29,45,0.8);
}

.measureLeft img {
    width: 372px;
    height: 71px;
    margin: 82px auto;
}

.measureLeft button {
    width: 120px;
    height: 36px;
    background: #BA8A53;
    border-radius: 2px;
    position: absolute;
    left: 280px;
    line-height: 36px;
    text-align: center;
    color: #fff;
    bottom: 80px;
    cursor: pointer;
    border: none;
}

.measureLeft button:focus {
    outline: none;
}

/* 图片放大特效 */

.zoomOut {
    animation: zoomOut .3s forwards;
}

@keyframes zoomOut {
    from {
        opacity: 1;
        transform: scale(1)
    }
    to {
        opacity: 0;
        transform: scale(1.5)
    }
}

@keyframes caculateDown {
    from {
        bottom: 80px;
    }
    to {
        bottom: 30px;
    }
}

/* 按钮下移 */

.caculateDown {
    animation: caculateDown .3s forwards;
}

/* 计算样式 填表处 */
.measure .lcy-kuang img{
    width:20px;
    height: 20px;
    margin:0;
}
.personal-information {
    width: 600px;
    margin: 20px auto;
}

.personal-information .lcy-kuang {
    width: 240px;
    position: relative;
    height: 40px;
    margin: 10px 30px;
    /* border:1px solid rgb(208,210,212); */
    border-radius:3px;
    border:1px solid rgba(208,210,212,1);
}
.iconfontcolor{
    color:#000;
}
.apply-icon {
    width: 20px;
    position: absolute;
    left: 10px;
    top: 10px;
    z-index: 5;
}
/* 计算结果 */
.result-box {
    color:#fff;
    text-align: center;
}
.result-box .result-title {
    font-size: 20px;
    line-height: 20px;
    font-weight: 400;
    padding:50px 0 65px;
    animation: slideIn .5s forwards;
}
.result-box .result-body{
    font-size: 40px;
    line-height: 40px;
    font-weight: 500;
    padding-bottom:59px;
    animation: fadeIn .5s forwards;
}
.result-box .result-btn{
    width: 120px;
    height: 36px;
    line-height: 36px;
    background: #fff;
    color:#C2955D;
    border-radius:4px;
    margin:0 auto;
    cursor: pointer;
    animation: fadeIn .5s forwards;
}
.measure .caculateLoading{
    display: block;
    width: 60px;
    height: 60px;
    position: absolute;
    top:0;
    left:0;
    bottom:0;
    right:0;
    margin:auto;
}
@keyframes slideIn {
    from {
        transform: translateY(-100px);
    }
    to {
        transform: translateY(0);
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

/* 计算样式 填表处结束 */

.measureRight {
    width: 300px;
    height: 300px;
    margin-left: 20px;
    background: #fff;
    border-radius: 3px;
}

