}

@keyframes fade-in {
    0% {
        opacity: 0;
    }
    /*初始状态 透明度为0*/
    40% {
        opacity: 0;
    }
    /*过渡状态 透明度为0*/
    60% {
        opacity: 0.3;
    }
    80% {
        opacity: 0.6;
    }
    100% {
        opacity: 1;
    }
    /*结束状态 透明度为1*/
}

.DB12 {
    animation: fade-in 2s;
    animation-delay: 0s;
    position: absolute;
    left: 354px;
    top: 310px;
    width: 292px;
}

.DB13 {
    animation: fade-in 2s;
