.products p{
    margin:0;
}
.banner{
    /* max-width: 1440px; */
    width:100%;
    margin:0 auto;
    height: 400px;
}
.banner img {
    /* width: 100%; */
    display: block;
}

.products {
    background: #fff;
}

.main {
    width: 1000px;
    margin: 0 auto;
}

/*申请流程*/

.lcy-flow-diagram {
    width: 100%;
    background: #fff;
    padding-bottom: 70px;
}

.lcy-little-title {
    text-align: center;
    padding: 40px 0 19px;
}

.lcy-little-title i {
    width: 6px;
    height: 16px;
    margin-right: 9px;
    background: #C2955D;
    display: inline-block;
}

.lcy-little-title span {
    font-size: 18px;
    color: #333;
}

.lcy-flow-diagram img {
    width: 682px;
    display: block;
    margin: 0px auto;
}

/* 顶部样式 */
.lcy-banner-cont {
    width: 800px;
    top:128px;
    left:50%;
    position: absolute;
    transform: translateX(-50%);
}

.lcy-details-type {
    width: 100%;
}

.lcy-details-type .lcy-details-name {
    font-size: 30px;
    line-height: 36px;
    color: #fff;
    display: block;
}

.lcy-details-type .lcy-details-name i {
    font-size: 22px;
    color: #fff;
    display: block;
}

.lcy-details-type .lcy-details-apply {
    width: 120px;
    height: 36px;
    background: #BA8A53;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    border-radius: 4px;
    line-height: 36px;
    position: relative;
    margin: 2px 0;
}

.lcy-details-type .lcy-details-apply span {
    font-size: 18px;
    color: #fff;
    text-align: center;
    width: 100%;
    display: block;
    cursor: pointer;
}

.lcy-details-type .lcy-details-apply img {
    width: 10px;
    position: absolute;
    top: 13px;
    right: 8px;
}

.lcy-details-introduce {
    width: 100%;
    margin: 28px 0;
}

.lcy-details-introduce ul {
    width: 798px;
    text-align: center;
    border: 1px solid #C2955D;
    border-radius: 2px;
    height: 140px;
    background: rgba(0, 0, 0, .6) url(../../assets/images/140@3x.png) no-repeat 100% 0;
    background-size: 18% 100%;
}

.lcy-details-introduce li {
    width: 196px;
    height: 100px;
    margin: 20px 0;
    padding: 8px 0 24px;
    float: left;
    border-right: 1px solid rgba(255, 255, 255, 0.2);
}
.lcy-details-introduce li:last-child {
    border:0;
}

.lcy-details-introduce li span {
    font-size: 14px;
    color: #999;
    display: block;
    width: 100%;
    line-height: 50px;
}

.lcy-details-introduce li p {
    font-size: 22px;
    color: #fff;
    width: 90%;
    height: 48px;
    margin: 0 auto;
}

.lcy-details-introduce li p.more {
    line-height: 30px;
}

.lcy-claim {
    overflow: hidden;
}

.lcy-claim-cont {
    width: 798px;
    margin: 40px auto 60px;
    border: 1px solid #ECECEC;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    -ms-border-radius: 2px;
    -o-border-radius: 2px;
    border-radius: 2px;
    /* height: 300px; */
    overflow: hidden;
}

.lcy-claim-cont ul {
    width: 580px;
    margin: 10px auto;
}

.lcy-claim-cont ul li i {
    width: 4px;
    height: 4px;
    -webkit-border-radius: 5px;
    -moz-border-radius: px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    border-radius: 5px;
    background: #C2955D;
    float: left;
    margin: 21px 10px;
}

.lcy-claim-cont ul li p {
    /*float: left;*/
    font-size: 16px;
    color: #C2955D;
    line-height: 28px;
    width: 94%;
    margin: 0 auto;
}

.lcy-claim-cont img {
    width: 200px;
    margin: 5px;
}

/* 展示的下拉框 */
.down-icon-erwm {
    width: 440px;
    background: #fff;
    position: absolute;
    top: 38px;
    right:0;
    z-index: 999;
}

.down-icon-erwm p {
    color: #666;
    font-size: 14px;
    text-align: center;
    padding: 15px 0 9px;
    margin:0;
}

.down-icon-erwm .erw-img {
    float: left;
    width: 50%;
    margin: 0 auto;
    text-align: center;
    position: relative;
}

.down-icon-erwm .erw-img i {
    position: absolute;
    border-right: 1px dashed #ECECEC;
    height: 160px;
    display: block;
    top: 10px;
    right: 0;
}

.down-icon-erwm .erw-img img {
    display: block;
    margin: 0 auto 0;
    width: 140px;
    height: 140px;
}

.down-icon-erwm .erw-img p {
    color: #C2955D;
    font-size: 14px;
    padding: 7px 0 15px;
}
