/* 费用计算 */
.lcy-cost {
    width: 100%;
    background: #f8f8f8;
    min-width: 1100px;
    overflow: hidden;
}

.lcy-cost .lcy-cost-cont {
    width: 800px;
    margin: 0 auto;
}

.lcy-cost-result {
    border: 1px solid #ECECEC;
    background: #fff;
    width: 100%;
    /* height: 80px; */
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    -ms-border-radius: 2px;
    -o-border-radius: 2px;
    border-radius: 2px;
    padding: 20px 0;
    margin-bottom: 40px;
    display: inline-block;
}

.lcy-should-repay {
    width: 720px;
    margin: 0 auto;
}

.lcy-should-repay p {
    font-size: 16px;
    color: #333;
    min-width: 220px;
    float: left;
    margin-right: 30px;
}

.lcy-should-repay p span {
    font-size: 22px;
    color: #333;
    font-weight: bold;
}

.lcy-cost-parameter {
    width: 720px;
    margin: 16px auto;
}

.lcy-cost-parameter input {
    height: 36px;
    line-height: 36px;
    font-size: 16px;
    color: #333;
    width: 190px;
    padding-left: 10px;
    text-shadow: none;
    border:none;
    background:rgba(248,248,248,1);
    border-radius:2px;
    border:1px solid rgba(236,236,236,1);
}

.lcy-cost-parameter .lcy-money {
    width: 220px;
    margin-right: 30px;
}

.lcy-cost-parameter .lcy-duration {
    width: 270px;
    margin-right: 70px; 
    /* border: 1px solid #ECECEC; */
    background: #F8F8F8;
    border-radius: 2px;
}

.lcy-cost-parameter .lcy-duration span {
    width: 90px;
    height: 36px;
    line-height: 36px;
    text-align: center;
    border-right: 1px solid #ECECEC;
    color: #999;
    float: left;
    cursor: pointer;
}

.lcy-cost-parameter .lcy-duration .on {
    background: #C2955D;
    color: #fff;
}

.lcy-cost-parameter .lcy-determine {
    width: 120px;
    height: 36px;
    color:#fff;
    line-height: 36px;
    font-size: 18px;
    text-align: center;
    background: #C2955D;
    cursor: pointer;
}