.home-measure .ant-select-selection {
    border:0;
    height: 38px;
}
.home-measure .ant-select-selection:active {
    border-shadow: none;
}
.home-measure .ant-select-selection-selected-value{
    float: none;
    line-height: 40px;
}
.banner-app-code{
    padding: 0 0 16px;
    color:#fff;
    font-size: 18px;
    line-height: 42px;
    text-align: center;
    background: rgba(0,0,0,.5);
}
.banner-app-code img{
    width: 140px;
    display: block;
    margin: auto;
}