*{
    margin:0;
    padding:0;
}
body{
    background:rgba(248,248,248,1);
}
li{
    list-style:none;
}
.clickwidth{
    width:1000px;
    margin:20px auto 68px auto;
    background:#fff;
    min-width:1000px;
    border-radius:2px;
    box-shadow:0px 1px 2px 0px rgba(0,0,0,0.1);
}
.clickwidth:after{
    clear:both;
    content:'';
    display:block;
    width:0;
    height:0;
    visibility:hidden;
}
/* 公司介绍 */
.left_introduction{
    width:20%;
    float:left;
}
.left_introduction li{
    height:50px;
    line-height:50px;
    font-size:16px;
    text-align:center;
    cursor:pointer;
}
.intro_active{
    background:rgba(227,233,238,1);
    color:#333333;
    font-weight:bold;
}
.right_contents{
    width:80%;
    float:left;
    /* padding:0 7% 0 7%; */
    padding:0 30px;
    border-left:1px solid rgb(231,231,231);
}
/* 公司介绍 */
.about_us img,.rest_contor img{
    width:100%;
}
.us_title{
    font-size:18px;
    color:rgba(51,51,51,1);
    line-height:54px;
}
.listofTit{
    font-size:14px;
    color:rgba(51,51,51,0.8);
    line-height:22px;
    margin-bottom:30px;
}
.flexAuto{
    width:100%;
    display:flex;
    flex-direction:row;
    justify-content:space-between;
    flex-wrap:wrap;
}
.flexAutoList{
    width:210px;
}
.flexAutoList>p{
    text-align:center;
    line-height:30px;
}
.BottomMargin{
    margin-bottom:70px;
    display:flex;
    flex-direction:column;
    align-items:center;
    position:relative;
}
.mapContainer{
    width:740px;
    height:400px;
    background:pink;
    margin-bottom:70px;
    display:flex;
    flex-direction:column;
    align-items:center;
    position:relative;
}
.hoverclass:hover{
    background:rgba(227,233,238,1);
    color:#333333;
    font-weight:bold;
}
/* 风控管理介绍 */
.rest_contor{
    height:53px;
    line-height:53px;
    font-size:18px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(51,51,51,1);
    border-bottom:1px solid rgba(227,233,238,1);
}
/* 联系我们 */
.contentTextStyle,.mailbox{
    font-size:14px;
    color:rgba(51,51,51,0.6);
    line-height:14px;
    margin:20px auto 20px auto;
}
.contentTextStyle p>span{
    color:#333333;
}
.mailbox p{
    color:rgba(51,51,51,1);
}
.mailbox p>text{
    color:#036EB7;
}
/* 关于我们招贤纳士 */
.descriptionair div{
    display:inline-block
}
.right_line{
    width:602px;
    height:1px;
    background:rgba(227,233,238,1);
}
.Banders{
    margin:0;
    color:#666;
    line-height:25px;
}

/* 网点布局样式开始 */
.provnice{
    width:700px;
    height:150px;
    border-radius:2px;
    margin-top:21px;
    border:1px solid rgba(194,149,93,1);
    display:flex;
    flex-direction:row;
    align-items:center;
    justify-content:space-between;
}
.provniCecenter{
    width:225px;
    height:107px;
    text-align:center;
    border-right:1px solid rgba(236,236,236,0.5);
}
.provnice .provniCecenter:last-child{
    border-right:0;
}
.provniCecenter span{
    font-size:14px;
    color:rgba(153,153,153,1);
    line-height:16px;
}
.provniCecenter p{
    font-size:40px;
    font-family:PingFangSC-Semibold;
    font-weight:600;
    color:rgba(194,149,93,1);
    line-height:40px;
    margin:20px 0;
}
.bottomYellow{
    width:94%;
    margin:0 auto;
    color: #f4e925;
    padding: 5px 0;
    line-height: 16px;
    float: left;
    position:absolute;
    bottom:10px;
}
.bottomYellow p{
    width:15px;
    height:15px;
    background:#f4e925;
    margin-right:10px;
    border-radius:100%;
}
.bottomYellow p,.bottomYellow div{
    float: left;
}
/* 点击img放大图片 */
.Magnified_Pics{
    width:100%;
    height:100%;
    background: rgba(0,0,0,.5);
    position:fixed;
    top:0;
    left:0;
}
.pictuRes{
    position:fixed;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    width:960px;
    height:600px;
    background:#bfbfbf;
    text-align:center;
}
.closeall{
    position: absolute;
    right: -90px;
    background: white;
    padding: 20px;
}
.leftClicks{
    position: absolute;
    left: 10px;
    top: 280px;
    width: 25px;
    height: 40px;
    line-height: 40px;
}
.rightClicks{
    position: absolute;
    right: 10px;
    top: 280px;
    width: 25px;
    height: 40px;
    line-height: 40px;
}
.The_picture{
    vertical-align: middle;
    max-width: 800px;
    max-height: 600px;
    display: inline-block;
}

#chart{
    width:600px;
    height:463px;
    position:relative;
    top:52px;
    /* background:pink; */
}
Table{
    cursor: pointer;
}