@import '~antd/dist/antd.css';
body {
    height: auto;
}

.fl {
    float: left;
}

.fr {
    float: right;
}

body {
    min-width: 1200px;
}

a,
a:focus {
    text-decoration: none;
}

li {
    list-style: none;
}

ul {
    margin: 0;
}

/*index顶部*/

.zsqy-index-header {
    width: 100%;
    min-width: 1100px;
    height: 60px;
    line-height: 60px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
}

.header-bj {
    background: #fff;
    box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.1);
}

.header-bj-home {
    background: transparent;
}
.namescolor{
    border-bottom:4px solid #000;
    color:#fff;
}
.zsqy-index-header .header-content {
    width: 1010px;
    margin: 0 auto;
}

.zsqy-index-header .logo img {
    width: 112px;
    height: 40px;
    margin: 10px 10px 10px 0;
}

.zsqy-index-header .header-nav a {
    font-size: 16px;
    /* padding: 0 10px; */
    margin:0 10px;
    display: inline-block;
    height: 60px;
    color: #666;
    border-bottom-color: transparent;
}

.zsqy-index-header .header-nav-home a {
    font-size: 16px;
    color: #fff;
    /* padding: 0 10px; */
    margin:0 10px;
    margin:0 10px;
    display: inline-block;
    height: 60px;
    border-bottom-color: transparent;
}
.zsqy-index-header .header-nav .active {
    border-bottom: 4px solid #C2955D;
    color: #C2955D;
}
.classactive{
    border-bottom: 4px solid #C2955D;
    color: #C2955D;
}

/*index底部*/

.zsqy-index-footer {
    background: #333;
    height: 300px;
    min-width: 1100px;
}

.zsqy-index-footer .index-footer-top {
    width: 100%;
    background: #333;
    height: 260px;
    padding: 30px 0 0;
}

.zsqy-index-footer .index-footer-top .interlinkage {
    width: 1000px;
    margin: 0 auto;
}

.zsqy-index-footer .index-footer-top .footer-list {
    font-size: 14px;
    border-right: 1px solid #444;
    height: 140px;
    overflow: hidden;
    /*width:100px;*/
    padding: 0 30px;
}

.zsqy-index-footer .index-footer-top .footer-list:first-child {
    padding-left: 0
}

.zsqy-index-footer .index-footer-top .footer-list a {
    display: block;
    color: #999;
    line-height: 28px;
    /*width: 140px;*/
    text-align: left;
}

.zsqy-index-footer .index-footer-top .footer-list .classify {
    font-size: 16px;
    color: #fff;
}

.zsqy-index-footer .index-footer-top .footer-list2 {
    border-right: none;
    width: 350px;
    padding-right: 0;
}

.zsqy-index-footer .index-footer-top .footer-list2 span {
    display: inline-block;
    /* width: 150px; */
    color: #999;
    margin-bottom: 8px;
}

.zsqy-index-footer .footer-ma {
    width: 150px;
    text-align: center;
    color: #fff;
    font-size: 16px;
}

.zsqy-index-footer .footer-ma img {
    width: 140px;
}

.zsqy-index-footer .index-footer-bottom {
    height: 40px;
    line-height: 40px;
    background: #222;
    font-size: 12px;
    color: #C2955D;
    text-align: center;
}

/* 回到顶部样式 */

.ant-back-top-content {
    width: 60px;
    border-radius: 0;
}

/* 首页新闻轮播样式 */

.home-news .slick-dots {
    bottom: 22px;
    height: 12px;
    padding-right: 20px;
    text-align: right;
}

.home-news .ant-carousel .slick-dots button {
    /* display: none !important; */
    display: block;
    height: 100%;
    width: 100%;
    background: rgba(255, 255, 255, 1);
}

.home-news .slick-dots li {
    width: 12px;
    height: 6px;
    border-radius: 3px;
    margin-right: 4px;
    opacity: 0.5;
    overflow: hidden;
}

.home-news .slick-dots li.slick-active {
    opacity: 1;
}

.home-news .slick-dots li.slick-active button {
    background: #C2955D;
    width: 100%;
}

/* 首页新闻tab */

.news-tab .ant-tabs-tab {
    font-size: 16px;
    font-weight: 500;
}

.news-tab .ant-tabs-tab-active {
    color: rgb(194, 149, 93);
}

.news-tab .ant-tabs-ink-bar {
    background: rgb(194, 149, 93);
}

.news-tab .ant-tabs-extra-content {
    padding-top: 4px;
}

.news-tab .ant-tabs-extra-content a {
    font-size: 16px;
    font-weight: 400;
    color: rgb(153, 153, 153);
}
body .news-tab .ant-tabs-nav .ant-tabs-tab{
    color:#999;
}