.aLLcontentIMG{
    position:fixed;
    left:0;
    top:0;
    width:100%;
    height:100%;
    /* background:rgba(248,248,248,0.9); */
    z-index:999;
    transition: transfrom .8s ease-in,top .8s  ease-out;
    display:block;
}
.aLLcontentIMGS{
    display:none;
}
.BodyIMG{
    width:1010px;
    height:218px;
    /* background:pink; */
    margin:60px auto;
    position:relative;
    -webkit-animation: fadeInDown .5s;
    animation: fadeInDown .5s;
}
.BodyIMG>img{
    width:408px;
    height:218px;
    position:absolute;
    margin-top:-11px;
    /* right:30px; */
    right:-70px;
}
/* .fadeIn {
    -webkit-animation: fadeInDown .3s;
    animation: fadeInDown .3s;
} */

@keyframes fadeInDown {
    0% {
        -webkit-transform: translate3d(0, -20%, 0);
        -webkit-transform: translate3d(0, -20%, 0);
        transform: translate3d(0, -20%, 0);
        transform: translate3d(0, -20%, 0);
        opacity: 0;
    }
    100% {
        -webkit-transform: none;
        transform: none;
        opacity: 1;
    }
}

@-webkit-keyframes fadeInDown {
    0% {
        -webkit-transform: translate3d(0, -20%, 0);
        opacity: 0;
    }
    100% {
        -webkit-transform: none;
        opacity: 1;
    }
}
